* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.card-img {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}
img {
  width: 80px;
  height: 45px;
}
span {
  margin-right: 10px;
  font-weight: bolder;
  font-size: 18px;
}
